    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

@media only screen and (max-width: 991px) {
  header .container {
    width: 100%;
    padding: 0;
  }
}

@media only screen and (max-width: 991px) {
  header {
    padding-bottom: 0;
  }
}

@media only screen and (max-width: 1199px) {
  .logo {
    text-align: center;
  }
}

@media only screen and (min-width: 1200px) {
  .logo img {
    display: block;
  }
}

.nav-block {
  position: relative;
}

.contact-block {
  padding: 20px;
}

@media only screen and (min-width: 992px) {
  .contact-block {
    width: 150px;
    flex-shrink: 0;
    margin: 0 30px 0 40px;
    padding: 0;
  }
}

.contact-block a {
  display: block;
  font-size: 18px;
  color: #fff;
  text-decoration: none;
  font-weight: 600;
}

@media only screen and (min-width: 992px) {
  .contact-block a {
    color: #000;
  }
}

.contact-block span {
  color: #ae8f60;
  font-size: 13px;
  display: block;
}

.nav-controls {
  display: flex;
  align-items: center;
}

.nav-controls .toggle-menu, .nav-controls .btn {
  height: 70px;
}

.nav-controls .toggle-menu {
  background: #000;
  width: 70px;
  display: inline-flex;
  justify-content: center;
  align-content: center;
  outline: none;
  flex-wrap: wrap;
  border: 0;
  z-index: 100;
  position: relative;
}

.nav-controls .toggle-menu:after {
  z-index: 100;
  content: "";
  width: 17px;
  height: 17px;
  background: url("../img/icons/close.png") no-repeat 0 0;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-20px, -50%);
  transition: all 0.3s linear 0s;
}

.nav-controls .toggle-menu span {
  display: block;
  width: 35px;
  height: 2px;
  background: #fff;
  opacity: 1;
  transition: all 0.3s linear 0s;
}

.nav-controls .toggle-menu span + span {
  margin-top: 5px;
}

.nav-controls .toggle-menu.toggled span {
  opacity: 0;
}

.nav-controls .toggle-menu.toggled:after {
  opacity: 1;
  transform: translate(-50%, -50%);
}

.nav-controls .btn {
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (min-width: 992px) {
  .nav-controls {
    display: none;
  }
}

.nav-collapse {
  transition: all 0.3s linear 0s;
}

@media only screen and (min-width: 992px) {
  .nav-collapse {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

@media only screen and (max-width: 991px) {
  .nav-collapse .btn {
    display: none;
  }
}

@media only screen and (max-width: 991px) {
  .nav-collapse {
    background: #000;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    padding-top: 70px;
    opacity: 0;
    visibility: hidden;
  }
}

.nav-collapse.show {
  opacity: 1;
  visibility: visible;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

@media only screen and (min-width: 992px) {
  nav ul {
    display: flex;
  }
}

nav ul li a {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
  padding: 20px;
  text-decoration: none;
  transition: all 0.3s linear 0s;
  color: #fff;
  font-size: 15px;
  border-bottom: solid 1px rgba(204, 204, 204, 0.24);
}

@media only screen and (min-width: 992px) {
  nav ul li a {
    color: #333;
    border-right: solid 1px #ccc;
    padding: 3px 20px;
    border-bottom: 0;
  }
}

@media only screen and (min-width: 1200px) {
  nav ul li a {
    padding: 3px 25px;
  }
}

nav ul li a:hover, nav ul li a:focus {
  color: #e19d36;
}

nav ul li:first-child a {
  border-top: solid 1px rgba(204, 204, 204, 0.24);
}

@media only screen and (min-width: 992px) {
  nav ul li:first-child a {
    border-left: solid 1px #ccc;
    border-top: 0;
  }
}

@media only screen and (min-width: 992px) {
  .toggle-menu {
    display: none;
  }
}

